import React, { Component } from "react";
import { connect } from "react-redux";
import Footer from "../Footer/Footer";
import { signin, setError } from "../actionCreators";

class Login extends Component {
  state = {
    email: "",
    password: "",
    allowSubmit: false
  };

  checkInputs = () => {
    if (this.state.email.length > 0 && this.state.password.length > 0) {
      this.setState({
        allowSubmit: true
      });
    } else {
      this.setState({
        allowSubmit: false
      });
    }
  };

  emailInput = event => {
    this.setState(
      {
        email: event.target.value
      },
      () => {
        this.checkInputs();
      }
    );
  };

  passwordInput = event => {
    this.setState(
      {
        password: event.target.value
      },
      () => {
        this.checkInputs();
      }
    );
  };

  signin = () => {
    var reqBody = {
      email: this.state.email,
      password: this.state.password
    };

    this.props.signin(reqBody);
  };

  render() {
    const ErrorMsg = () => (
      <div
        style={{
          backgroundColor: "red",
          color: "white",
          textAlign: "center",
          padding: 15,
          fontWeight: "bold"
        }}
      >
        {" "}
        {this.props.error.msg}
      </div>
    );
    return (
      <div
        style={{ minHeight: "100%", paddingBottom: 120, position: "relative" }}
      >
        {this.props.error.active ? <ErrorMsg /> : null}
        <div style={{ padding: "0px 15%" }}>
          <div className="route-title">Iniciar sesin</div>
          <input
            type="text"
            className="boletin-input"
            placeholder="Email"
            value={this.state.email}
            onChange={this.emailInput}
          />
          <input
            type="text"
            className="boletin-input"
            placeholder="Contrasea"
            value={this.state.password}
            onChange={this.passwordInput}
          />
          <div>
            <button
              className={
                this.state.allowSubmit ? "submit-boletin" : "disabled-btn"
              }
              onClick={this.state.allowSubmit ? this.signin : null}
            >
              Iniciar sesin
            </button>
          </div>
        </div>
        <Footer />
      </div>
    );
  }
}

const mapStateToProps = state => ({
  error: state.activeError
});

const mapDispatchToProps = dispatch => ({
  signin: body => {
    dispatch(signin(body));
  },
  clearError() {
    dispatch(setError({ active: false, msg: "" }));
  }
});

export default connect(mapStateToProps, mapDispatchToProps)(Login);



