import { combineReducers } from "redux";
import {
  GET_BOLETINES,
  GET_ARTICLES,
  GET_CATEGORIES,
  SUCCESS,
  UPLOAD_AUDIO,
  SET_TOKEN,
  SET_USER,
  SET_LOGGED,
  SET_ERROR
} from "./actions";

const CLEAR_ERROR = "CLEAR_ERROR";

const activeError = (state = { active: false, msg: "" }, action) => {
  if (action.type === SET_ERROR) {
    return action.payload;
  } else if (action.type === CLEAR_ERROR) {
    return action.payload;
  } else {
    return state;
  }
};

const jwt = (state = "", action) => {
  switch (action.type) {
    case SET_TOKEN:
      return action.payload;
    default:
      return state;
  }
};

const loggedUser = (state = {}, action) => {
  switch (action.type) {
    case SET_USER:
      return action.payload;

    default:
      return state;
  }
};

const logged = (state = false, action) => {
  switch (action.type) {
    case SET_LOGGED:
      return action.payload;

    default:
      return state;
  }
};

const boletines = (state = [], action) => {
  switch (action.type) {
    case GET_BOLETINES:
      return action.payload;

    default:
      return state;
  }
};

const articles = (state = [], action) => {
  switch (action.type) {
    case GET_ARTICLES:
      return action.payload;

    default:
      return state;
  }
};

const categories = (state = [], action) => {
  switch (action.type) {
    case GET_CATEGORIES:
      return action.payload;

    default:
      return state;
  }
};

const uploading = (state = "", action) => {
  if (action.type === UPLOAD_AUDIO) {
    return action.payload;
  } else {
    return state;
  }
};

const success = (state = false, action) => {
  switch (action.type) {
    case SUCCESS:
      return action.payload;
    default:
      return state;
  }
};

const rootReducer = combineReducers({
  boletines,
  articles,
  categories,
  success,
  uploading,
  logged,
  jwt,
  loggedUser,
  activeError
});

export default rootReducer;



