import React from "react";
import {
  BrowserRouter,
  Route,
  Switch,
  Redirect,
  withRouter
} from "react-router-dom";
import { Provider, connect } from "react-redux";
import Boletines from "./Boletines/Boletines";
import Login from "./Login/Login";
import Editor from "./PostArticle/PostArticle";
import PostBoletin from "./PostBoletin/PostBoletin";
import Boletin from "./Boletin/Boletin";
import Categorias from "./Categorias/Categorias";
import PostCategory from "./PostCategory/PostCategory";
import Article from "./Article/Article";
import createStore from "./store";
import { signin, setLogged, getUser, setToken } from "./actionCreators";
import { PersistGate } from "redux-persist/integration/react";

const { store, persistor } = createStore();
class PrivateRoute extends React.Component {
  componentDidMount() {
    console.log(this.props.token);
    if (!this.props.logged && this.props.token.length > 10) {
      this.props.checkToken(this.props.token);
      console.log("made it here");
    }
  }

  render() {
    const { component: Component, ...rest } = this.props;
    console.log(rest);
    return (
      <Route
        {...rest}
        render={props => {
          if (this.props.logged) {
            return <Component {...props} />;
          } else {
            return <Login />;
          }
        }}
      />
    );
  }
}

const mapStateToProps = state => {
  return {
    logged: state.logged,
    token: state.jwt,
    loggedUser: state.loggedUser
  };
};

const mapDispatchToProps = dispatch => {
  return {
    setLogged: () => {
      dispatch(setLogged(true));
    },
    signin: body => {
      dispatch(signin(body));
    },
    checkToken: token => {
      dispatch(getUser(token));
    },
    setToken: token => {
      dispatch(setToken(token));
    }
  };
};
const ConnectedRoute = withRouter(
  connect(mapStateToProps, mapDispatchToProps)(PrivateRoute)
);

const App = () => (
  <Provider store={store}>
    <PersistGate loading={null} persistor={persistor}>
      <BrowserRouter>
        <Switch>
          <Redirect from="/" exact to="/boletines" />
          <ConnectedRoute
            path="/boletines"
            exact
            component={() => <Boletines activeTab={1} />}
          />
          <ConnectedRoute path="/categorias" exact component={Categorias} />
          <ConnectedRoute
            path="/post-article/:boletin"
            exact
            component={Editor}
          />

          <ConnectedRoute
            path="/boletines/post"
            exact
            component={PostBoletin}
          />
          <ConnectedRoute path="/boletines/:id" exact component={Boletin} />
          <ConnectedRoute
            path="/categorias/post"
            exact
            component={PostCategory}
          />
          <ConnectedRoute path="/article/:id" exact component={Article} />
        </Switch>
      </BrowserRouter>
    </PersistGate>
  </Provider>
);

/*      <Route path="/categorias" exact component={} />
      <Route path="/boletines/:id" exact component={} />
      <Route path="/boletines/upload" exact component={} />
      <Route path="/categorias/upload" exact component={} />
      <Route path="/article/upload" exact component={}/>
      <Route path="/article/:id" exact component={}/>*/

export default App;



